import { OthreqComponent } from './othreq/othreq.component';
import { UsersdashboardComponent } from './usersdashboard/usersdashboard.component';
import { UserstatusComponent } from './userstatus/userstatus.component';

import { UserrequestComponent } from './userrequest/userrequest.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'reqboard',
    pathMatch: 'full'
  },
  {
    path: 'req',
    component: UserrequestComponent
  },
  {
    path: 'reqs',
    component: UserstatusComponent
  },
  {
    path: 'othreq',
    component: OthreqComponent
  },
  {
    path: 'reqboard',
    component:  UsersdashboardComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
// tslint:disable-next-line: class-name
export class empboardRoutingModule {}
